.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

/* additional css */

.data-select {
  width: 100%;
  margin-bottom: 20px;
  text-align: center;
}

.data-select-btn {
  text-align: center;
}

.chart-title {
  text-align: left;
}

.directions-text {
  text-align: justify;
}

.chart-pane {
  float: right;
  width: 600px;
  /* width: 100%; */
  margin-left: 30px;
  /* margin-bottom: 50px; */
  height: 100%;
}

.data-pane {
  width: 170px;
  /* float: left; */
  padding: 0px 5px 10px 5px;
  text-align: left;
  display: flex;
  flex-direction: column;
  /* background-color: lightgray; */
}

.input-grp>* {
  min-width: 100px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 5px 5px 5px 0px;
}

.input-field {
  width: 100%
}

.btn-container {
  /* float: left */
  /* margin-top: 5px; */
}

.btn-container>* {
  margin-top: 10px;
  display: block;
  width: 100%;
}

.function-value-label-span {
  font-weight: bold;
  padding: 5px 0px;
}

.function-value-span {
  width: 100%;
  /* margin-left: 10px; */
  padding: 5px 0px 0px 10px;
  display: inline-block;
  background-color: lightgray;
}

.input-label-btn {
  min-width: 65px;
  display: inline-block;
  font-style: italic;
  text-align: left;
}

.directions-text {
  text-align: left;
}

.animation-div {
  overflow: scroll;
  overflow-y: hidden;
  display: flex;
  flex-wrap: wrap;
  max-height: 100%
}

.equation-heading-container {
  display: flex;
}

.equation-heading {
  font-style: italic;
  display: inline-flex;
  margin-right: 60px;
}

.visual-container {
  height: 200px;
  display: block;
  border: 1px solid;
  margin-left: 50px;
  margin-top: 10px;
}

@media only screen and (max-width: 840px) {
  .chart-pane {
    float: right;
    /* width: 600px; */
    width: 100%;
    margin-left: 30px;
    /* margin-bottom: 50px; */
    height: 100%;
  }

  .data-pane {
    width: 100%;
    /* float: left; */
    padding: 10px 5px 10px 5px;
    text-align: left;
    display: flex;
    flex-direction: column;
    /* background-color: lightgray; */
  }
}